<template>

	<div>
		<ul class="g_formList">

			<li class="formList_li" @click=""><span class="character"><b>*</b> 现工作单位名称</span>
				<input class="text" type="text" v-model="list.name" placeholder="" @input="checkStatus">
			</li>

			<li class="formList_li">
				<span class="character"><b>*</b> 公司注册时间</span>
				<div class="select_box" @click="showTime">
					<i class="icon"></i> {{list.register}}
				</div>
			</li>

			<li class="hint">
				<p>该单位需在全国红盾网可查询，中国红盾网网址为http://www.hongdunwang.com.cn/</p>
			</li>

			<li class="formList_li"><span class="character"><b>*</b> 现工作部门名称</span><input class="text" type="text" v-model="list.department" placeholder="" @input="checkStatus"></li>

			<li class="hint">
				<p>填写与报考工种证书相匹配的部门</p>
			</li>

			<li class="formList_li"><span class="character"><b>*</b> 现工作岗位</span><input class="text_big" v-model="list.position" type="text" placeholder="" @input="checkStatus"> </li>

			<li class="hint">
				<p>填写与报考工种证书相匹配的岗位</p>
			</li>

			<li class="formList_li" @click="showStayYear">
				<span class="character"><b>*</b> 累计工作年限</span>
				<input class="text_center" type="text" placeholder="" v-model="list.stayYear">
				<span class="character_r">年</span>
			</li>

			<li class="hint">
				<p>累计年限应满足所报工种年限，例如一级劳协需满足连续工作19年，则填写“19”</p>
			</li>

		
			<li class="formList_li" style="height: 1.8rem;"><span class="character"><b>*</b> 单位所在市</span>

				<div class="select_box" style="width: 3.2rem;" @click="showProvince">
					<i class="icon"></i> {{list.region.province}}
				</div>
				<span style="float: left;margin-left: 0.3rem;">省</span>
				<div class="select_box" style="width: 3.2rem;margin-top: 0.2rem;" @click="showCity">
					<i class="icon"></i> {{list.region.city}}
				</div>
				<span style="float: left;margin-left: 0.3rem;position: relative;top: 0.2rem;">市</span>

			</li>
			<li class="formList_li">
				<span class="character"><b>*</b> 工作时间</span>
				<!--<select class="text_center" name="">
					<option value="">请选择</option>
					<option value="">...</option>
					<option value="">....</option>
				</select>-->
				<div class="select_box" @click="showStart">
					<i class="icon"></i> {{list.startTime}}
				</div>

				<span class="character_r">至今</span>
			</li>

			<li class="hint">
				<p>1. 根据鉴定中心审核要求各工作单位之间的间隔时间不得 超过1个月，该时间根据已填信息自动默认获取。 2. 所填写的工作时间应为该公司注册之后，在该公司注销之前，可通过红盾网查询注册时间和注销时间。</p>
			</li>

			<li class="formList_li"><span class="character"><b>*</b> 部门联系人</span><input class="text_big" type="text" v-model="list.contactName" placeholder="" @input="checkStatus"></li>

			<li class="hint">
				<p>部门联系人要写全称，能证明你的工作事实</p>
			</li>

			<li class="formList_li"><span class="character"><b>*</b> 部门办公电话</span><input class="text_big" type="text" v-model="list.contactMobile" placeholder="" @input="checkStatus"></li>

			<li class="hint">
				<p>必须是固话，必须能接通并证明你的工作事实</p>
			</li>
		</ul>

		<div class="button_lack" v-if="!inputAll">保存并返回</div>
		<div class="button_whole" v-if="inputAll" @click="submit">保存并返回</div>

	</div>

</template>

<script>
	

	 
	 
	export default {

		data() {
			return {
				list: {
					contactMobile: "",
					contactName: "",
					department: "",
					endTime: this.$store.state.nowYear + "-0" + this.$store.state.nowMonth,

					name: "",
					position: "",
					region: {
						id: "",
						regionInfo: "",
						provinceId: "",
						cityId: "",
						province: "",
						city: ""
					},
					register: "",
					startTime: "",
					stayYear: 0

				},
				province: [],
				city: [],
				inputAll: false,
				action: this.$route.params.action,
				nowYear: this.$store.state.nowYear,
				nowMonth:this.$store.state.nowMonth,
				stayYear: 0,
				startYear: 0,
				startMonth: 0,
				startDay: 0
			}
		},
		created() {
			this.getData(this.action)
			console.log(this.$store.state.nowYear)
			var yearArr = []
			for(var i = 1960; i <= this.nowYear; i++) {
				var obj = {
					value: i,
					text: i,
					children: []
				}
				for(var j = 1; j <= 12; j++) {
					if(j < 10) {
						var monthObj = {
							value: i,
							text: "0" + j
						}
					} else {
						var monthObj = {
							value: i,
							text: j
						}
					}

					obj.children.push(monthObj)
				}
				yearArr.push(obj)
			}
			console.log(yearArr)
//			this.start_time_picker = new mui.PopPicker({
//				layer: 2
//			});
			
			this.start_time_picker = new mui.DtPicker({"type":"date","beginYear":1960,"endYear":this.nowYear,"title":"32322"});
//			this.start_time_picker.setData(yearArr)
			this.getProvince()
           
		},
		methods: {

			getData(num) {
				var obj = {
					"s": this.$store.state.sessionId,
					"a": num

				}
				this.$Api.ExamUnitInfo(obj).then((res) => {
					console.log(res)
					if(res.q.info instanceof Array) {

					} else {
						console.log(res.q.info.region.regionInfo[1].region.name)
						this.list.contactMobile = res.q.info.contactMobile
						this.list.contactName = res.q.info.contactName
						this.list.department = res.q.info.department
                         
						this.list.name = res.q.info.name
						this.list.position = res.q.info.position
						this.list.region.province = res.q.info.region.regionInfo[0].region.name.split("省")[0]
						this.list.region.city = res.q.info.region.regionInfo[1].region.name.split("市")[0]
						this.list.register=res.q.info.registerTime
						this.list.startTime = res.q.info.startTime
						 this.checkStatus()
					}

				})

			},
			showTime() {
				var that = this
				this.start_time_picker.show(function(items) {
					console.log(items.value.split("-"))
					that.list.register = items.value
					that.startYear = items.value.split("-")[0]
					console.log(that.list.register)
					that.startMonth = items.value.split("-")[1]

					that.stayYear = that.nowYear - that.startYear
					console.log(that.stayYear)

					that.checkStatus()
				});

			},
			showStayYear() {
				if(this.list.register.length > 0) {
					var stayYear = new mui.PopPicker();
					var that = this
					var arr = []
					for(var i = this.stayYear; i >= 0; i--) {
						var obj = {
							value: i,
							text: "" + i
						}
						arr.push(obj)
					}
					stayYear.setData(arr);
					stayYear.show(function(items) {
						console.log(items)
						that.list.stayYear = items[0].text
						that.checkStatus()
					})

				} else {
					this.$toast.center("请先填写公司注册时间")
				}

			},
			showProvince() {
				var province = new mui.PopPicker();
				var that = this
				var arr = []
				province.setData(that.province);
				province.show(function(items) {
					console.log(items)
					that.list.region.province = items[0].text
					that.list.region.provinceId = items[0].value
					that.getCity(that.list.region.provinceId)
					that.checkStatus()
				})

			},
			showCity() {
				var city = new mui.PopPicker();
				var that = this
				var arr = []

				city.setData(that.city);
				city.show(function(items) {
					that.list.region.city = items[0].text
					that.list.region.cityId = items[0].value
					that.list.region.id = that.list.region.cityId
					that.list.region.regionInfo = that.list.region.province + "省" + that.list.region.city

					that.checkStatus()

				})
			},

			workYear() {
				var workYear = new mui.PopPicker();
				var that = this

			},
			getProvince() {
				var obj = {
					"s": this.$store.sessionId,
					"a": 1,
					"id": 0
				}
				this.$Api.RegionList(obj).then((res) => {
					console.log(res)
					for(var i = 0; i < res.q.items.length; i++) {
						var obj = {
							value: res.q.items[i].id,
							text: res.q.items[i].name
						}

						this.province.push(obj)

					}
					console.log(this.province)
				})

			},
			getCity(id) {
				var obj = {
					"s": this.$store.sessionId,
					"a": 2,
					"id": id
				}
				this.$Api.RegionList(obj).then((res) => {
					console.log(res)
					for(var i = 0; i < res.q.items.length; i++) {
						var obj = {
							value: res.q.items[i].id,
							text: res.q.items[i].name
						}

						this.city.push(obj)

					}
				})
			},

			showStart() {

				var workYear = []
				var perObj = {
					value: this.startYear,
					text: this.startYear,
					children: []
				}

				for(var k = Number(this.startMonth); k <= 12; k++) {
					if(k < 10) {
						var newMonthObj = {
							value: this.startYear,
							text: "0" + k
						}
					} else {
						var newMonthObj = {
							value: this.startYear,
							text: k
						}
					}

					perObj.children.push(newMonthObj)

				}
				workYear.push(perObj)
				for(var i = Number(this.startYear) + 1; i < this.nowYear; i++) {
					
					var obj = {
						value: i,
						text: i,
						children: []
					}
					for(var j = 1; j <= 12; j++) {
						if(j < 10) {
							var monthObj = {
								value: i,
								text: "0" + j
							}
						} else {
							var monthObj = {
								value: i,
								text: j
							}
						}

						obj.children.push(monthObj)
					}
					workYear.push(obj)
				}
				
				var nowObj={
					value: this.nowYear,
					text: this.nowYear,
					children: []
				}
				
				for(var z=1;z<=this.nowMonth;z++){
					if(z < 10) {
							var monthObj = {
								value: this.nowYear,
								text: "0" + z
							}
						} else {
							var monthObj = {
								value: this.nowYear,
								text: z
							}
						}
					
					nowObj.children.push(monthObj)
				}
				
				
				workYear.push(nowObj)
			
				
				console.log(workYear)
				var start_time_picker = new mui.PopPicker({
					layer: 2
				});
				start_time_picker.setData(workYear)

				var that = this

				start_time_picker.show(function(items) {
					console.log(items)
					that.list.startTime = items[1].value+"-"+items[1].text
					that.checkStatus()
				})

			},
			checkStatus() {
				console.log(this.list)
				if(this.list.contactMobile!=""||this.list.contactName!=""||this.list.department!=""||this.list.name!=""||this.list.position!=""||this.list.register!=""||this.list.startTime!=""||this.list.stayYear!=0){
					this.inputAll=true
				}else{
					this.inputAll=false
				}

			},
			submit(){
				var obj={
					"s":this.$store.state.sessionId,
					"info":{
						"sort":0,
						"name":this.list.name,
						"department":this.list.department,
						"position":this.list.position,
						"region":{
							"id":this.list.region.id,
							"regionInfo":this.list.region.regionInfo
						},
						"contactName":this.list.contactName+"",
						"contactMobile":this.list.contactMobile+"",
						"startTime":this.list.startTime,
						"endTime":this.list.endTime,
						"registerTime":this.list.register
					}
					
					
				}
				this.$Api.ExamUnitInfoSubmit(obj).then((res)=>{
					console.log(res)
				})
				
				
			}

		}
	}
</script>

<style scoped="scoped" lang="scss">
	@import "../../sass/baseSass.scss";
	input[type=text],
	input[type=password],
	input[type=submit],
	select {
		border: #cccccc 1px solid;
		height: .8rem;
		border-radius: .1rem;
		width: 100%;
	}
	
	input[type=text]:focus,
	input[type=password]:focus,
	input[type=submit]:focus,
	select:focus {
		border-color: #fa8a12;
	}
	
	input[type=text],
	input[type=password] {
		padding: 0 .1rem;
	}
	
	input[type=submit] {
		background-color: #fa8a12;
		text-align: center;
		width: 100%;
	}
	
	textarea {
		height: auto;
		min-height: 1.2rem;
		border: #cccccc 1px solid;
		border-radius: .1rem;
		padding: .2rem .3rem;
		width: 100%;
	}
	
	input:-moz-placeholder {
		color: #999999;
	}
	
	::-webkit-input-placeholder {
		color: #999999;
	}
	
	input[type=text],
	input[type=password] {
		-webkit-appearance: none;
	}
	
	.g_formList {
		padding: .3rem 0.3rem 0;
		.formList_li {
			padding-left: 2.3rem;
			position: relative;
			height: .7rem;
			margin-bottom: .3rem;
			font-size: .3rem;
			line-height: .7rem;
			.character {
				position: absolute;
				left: 0;
				color: #1a1a1a;
				font-weight: 600;
				b {
					color: #f32929;
				}
			}
			.character_r {
				float: right;
				margin-right: 0.55rem;
			}
			.text {
				height: .7rem;
				padding: 0 0.1rem;
				width: 3.5rem;
				float: right;
				margin-right: 0.55rem;
			}
			.text_center {
				height: .7rem;
				padding: 0 0.1rem;
				width: 3rem;
			}
			.texts {
				height: .7rem;
				padding: 0 0.1rem;
				width: 3rem;
				float: right;
				margin-right: 0.55rem;
			}
			.text_big {
				height: .7rem;
				padding: 0 0.1rem;
				width: 4rem;
				float: right;
				margin-right: 0.55rem;
			}
		}
		.hint {
			margin-bottom: .3rem;
			p {
				font-size: .25rem;
				color: #fa8a12;
				padding: 0 0.4rem 0 0.18rem;
			}
		}
		.formList_li_B {
			font-size: .3rem;
			height: .7rem;
			line-height: .7rem;
			margin-bottom: .3rem;
			.formList_li_div {
				display: inline-block;
				.character {
					color: #1a1a1a;
					font-weight: 600;
				}
				.text_c {
					width: 2.6rem;
				}
			}
			.div_padding {
				padding-left: 0.2rem;
			}
		}
	}
	
	.button_lack {
		width: 6.9rem;
		height: 1rem;
		background-color: #ccc;
		font-size: 0.34rem;
		color: #FFFFFF;
		text-align: center;
		line-height: 1rem;
		border-radius: 0.5rem;
		margin: 0.5rem auto 1.4rem;
	}
	
	.button_whole {
		width: 6.9rem;
		height: 1rem;
		background-color: #fa8a12;
		font-size: 0.34rem;
		color: #FFFFFF;
		text-align: center;
		line-height: 1rem;
		border-radius: 0.5rem;
		margin: 0.5rem auto 1.4rem;
	}
	
	.select_box {
		border: #cccccc 1px solid;
		height: 0.8rem;
		border-radius: .1rem;
		width: 3rem;
		float: left;
		margin-left: 0.2rem;
		position: relative;
		text-indent: 0.2rem;
		.icon {
			width: 0.2rem;
			height: 0.13rem;
			background: url(../../assets/tra.png) no-repeat;
			float: left;
			background-size: 100% 100%;
			position: absolute;
			right: 0.2rem;
			top: 0.35rem;
		}
	}
</style>


